<template>
  <div class="login_form">
    <div class="login_right">
      <Form
        v-if="allowRegister"
        ref="formInline"
        :model="formInline"
        :rules="ruleInline"
        inline
      >
        <div class="login_title">安全驗證</div>
        <p style="text-align: left; padding-right: 10px; font-size: 14px">
          電子郵件驗證碼
        </p>
        <FormItem prop="code">
          <Input
            type="text"
            v-model="formInline.code"
            :placeholder="$t('uc.regist.smscode')"
          >
          </Input>
          <input
            id="sendCode"
            type="Button"
            shape="circle"
            :value="sendcodeValue"
            :disabled="codedisabled"
          />
        </FormItem>
        <p
          style="
            color: #979797;
            float: left;
            padding-right: 10px;
            font-size: 12px;
          "
        >
          輸入發送給{{ userId }}的6位數代碼
        </p>

        <FormItem>
          <Button
            class="register_btn"
            @click="checksafe()"
            :disabled="registing"
            >提交</Button
          >
        </FormItem>
        <router-link
          to="/findPwd"
          style="
            color: #979797;
            float: left;
            padding-right: 10px;
            font-size: 12px;
          "
        >
          切換至其他驗證方式
        </router-link>
        <br />
        <router-link
          to="/findPwd"
          style="
            color: #979797;
            float: left;
            padding-right: 10px;
            font-size: 12px;
          "
        >
          安全驗證不可用?
        </router-link>
      </Form>
    </div>
  </div>
</template>
<style scoped lang="scss">
.login_form {
  background: #0b1520 url(../../assets/images/login_bg.png) no-repeat center
    center;
  height: 760px;
  position: relative;
  overflow: hidden;
  .login_right {
    padding: 20px 30px;
    position: absolute;
    background: #17212e;
    width: 350px;
    height: 350px;
    left: 50%;
    top: 50%;
    margin-left: -175px;
    margin-top: -205px;
    border-top: 4px solid #f0ac19;
    border-radius: 5px;
    .tel-title {
      color: #fff;
    }
    form.ivu-form.ivu-form-label-right.ivu-form-inline {
      .ivu-form-item {
        .ivu-form-item-content {
          .register_btn.ivu-btn {
            width: 100%;
            background-color: #f0ac19;
            outline: none;
            border-color: #f0ac19;
            color: #fff;
            border-radius: 5px;
            font-size: 18px;
            margin-top: 20px;
            &:focus {
              -moz-box-shadow: 2px 2px 5px transparent,
                -2px -2px 4px transparent;
              -webkit-box-shadow: 2px 2px 5px transparent,
                -2px -2px 4px transparent;
              box-shadow: 2px 2px 5px transparent, -2px -2px 4px transparent;
            }
          }
          .ivu-input-wrapper.ivu-input-type {
            .ivu-input {
              border: 1px solid red;
            }
          }
          #sendCode {
            position: absolute;
            border: 1px solid #f0ac19;
            background: transparent;
            top: 0px;
            outline: none;
            right: 0;
            width: 30%;
            color: #f0ac19;
            cursor: pointer;
          }
        }
      }
      .check-agree {
        color: #979797;
        display: inline-block;
        line-height: 30px;
        font-size: 12px;
        cursor: default;
        a {
          color: #f0ac19;
          margin-left: -10px;
        }
        .ivu-checkbox-wrapper.ivu-checkbox-wrapper-checked {
          .ivu-checkbox.ivu-checkbox-checked {
            .ivu-checkbox-inner {
              border: 1px solid #f0ac19;
              background-color: #f0ac19;
            }
          }
        }
      }
    }
  }
}

.login_title {
  text-align: center;
  height: 80px;
  font-size: 25px;
  color: #fff;
}

#captcha {
  width: 100%;
  display: inline-block;
}
.show {
  display: block;
}
.hide {
  display: none;
}
#notice {
  color: red;
}
#wait {
  text-align: left;
  color: #666;
  margin: 0;
}
.tel-title {
  font-size: 25px;
}
.login_left {
  display: none;
}
</style>
<script>
import gtInit from "../../assets/js/gt.js";
import $ from "jquery";
import { checkRegisterVerify, loginVerify } from "@/service/getData";
export default {
  name: "safeLogin",
  data() {
    const validateUser = (rule, value, callback) => {
      if (this.changeActive == 0) {
        // var reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
        // if (value == "") {
        //   callback(new Error(this.$t("uc.regist.teltip")));
        // } else if (!reg.test(this.formInline.user)) {
        //   callback(new Error(this.$t("uc.regist.telerr")));
        // } else {
        callback();
        // }
      } else {
        var reg =
          /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/;
        reg = /^(\w)+(\.\w+)*@(\w)+((\.\w{2,3}){1,3})$/;
        if (value == "") {
          callback(new Error(this.$t("uc.regist.emailtip")));
        } else if (!reg.test(this.formInline.user)) {
          callback(new Error(this.$t("uc.regist.emailerr")));
        } else {
          callback();
        }
      }
    };
    const validateRepassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(this.$t("uc.regist.confirmpwdtip")));
      } else if (value !== this.formInline.password) {
        callback(new Error(this.$t("uc.regist.confirmpwderr")));
      } else {
        callback();
      }
    };
    return {
      country: "美国",
      codedisabled: false,
      sendcodeValue: this.$t("uc.regist.sendcode"),
      isRegister: false,
      ticket: "",
      randStr: "",
      registing: false,
      captchaObj: null,
      modal1: false,
      _captchaResult: null,
      agree: true,
      allowRegister: true,
      buttonLists: [
        {
          text: this.$t("uc.regist.telregist"),
        },
        {
          text: this.$t("uc.regist.emailregist"),
        },
      ],
      areas: [],
      changeActive: 0,
      showCode: true,
      countdown: 60,
      formInline: {
        username: "",
        // country: "",
        user: "",
        code: "",
        areaCode: "",
        password: "",
        repassword: "",
        promotion: "",
      },
      ruleInline: {
        user: [{ validator: validateUser, trigger: "blur" }],
        code: [
          {
            required: true,
            message: this.$t("uc.regist.smscodetip"),
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: this.$t("uc.regist.pwdtip"),
            trigger: "blur",
          },
          {
            type: "string",
            min: 6,
            message: this.$t("uc.regist.pwdmsg"),
            trigger: "blur",
          },
        ],
        repassword: [{ validator: validateRepassword, trigger: "blur" }],
      },
      key: "",
      code: "",
    };
  },
  watch: {},
  computed: {
    isLogin: function () {
      return this.$store.getters.isLogin;
    },
    userId() {
      return this.$route.params.userId;
    },
    token() {
      return this.$route.params.token;
    },
    status() {
      return this.$route.params.status;
    },
  },
  created: function () {
    window.scrollTo(0, 0);
    // var oDiv = document.getElementById("example-navbar-collapse");
    // oDiv && (oDiv.className = "navbar-collapse collapse");
    this.init();
    this.actives(this.changeActive);
    if (
      this.$route.query.code != undefined &&
      this.$route.query.code != "" &&
      this.$route.query.code != null
    ) {
      this.formInline.promotion = this.$route.query.code;
    } else {
      this.formInline.promotion = "";
    }
  },
  methods: {
    checksafe() {
      let data = {
        input: this.userId,
        verification_token: this.token,
        verification_code: this.formInline.code,
      };
      //檢查code
      if (this.status == "add") {
        checkRegisterVerify(data)
          .then((rsp) => {
            if (rsp == "1") {
              this.$Notice.success({
                title: this.$t("common.tip"),
                desc: "驗證成功!",
              });
              this.$router.push("/login");
            } else {
              this.$Notice.error({
                title: this.$t("common.tip"),
                desc: rsp.message,
              });
            }
          })
          .catch((rsp) => {
            this.$Notice.error({
              title: this.$t("common.tip"),
              desc: rsp.message,
            });
          });
      } else if (this.status == "login") {
        loginVerify(data)
          .then((rsp) => {
            if (rsp.code == "1") {
              this.$Notice.success({
                title: this.$t("common.tip"),
                desc: "驗證成功!",
              });
              this.$store.commit("setToken", rsp.data.token.access_token);
              this.$store.commit("setMember", { user: this.userId });
              this.$router.push("/uc/safe");
            } else {
              this.$Notice.error({
                title: this.$t("common.tip"),
                desc: rsp.message,
              });
            }
          })
          .catch((rsp) => {
            this.$Notice.error({
              title: this.$t("common.tip"),
              desc: rsp.message,
            });
          });
      }
    },
    updateLangData() {
      this.buttonLists = [
        {
          text: this.$t("uc.regist.telregist"),
        },
        {
          text: this.$t("uc.regist.emailregist"),
        },
      ];

      if (this.changeActive == 0) {
        this.key = this.$t("uc.regist.telno");
      } else {
        this.key = this.$t("uc.regist.email");
      }
    },
    init() {
      this.$store.commit("navigate", "nav-other");
      this.$store.state.HeaderActiveName = "0";
      if (this.isLogin) {
        this.$router.push("/");
      }
      // this.getAreas();
      this.initGtCaptcha();
    },
    initGtCaptcha() {
      // 直接生成一个验证码对象
      // var self = this;
      // var captcha1 = new TencentCaptcha("2076680797", function(res) {
      //   res.ret == 0 &&
      //     (self.isRegister = true) &&
      //     (self.ticket = res.ticket) &&
      //     (self.randStr = res.randstr) &&
      //     self.success();
      // });
      // captcha1.show(); // 显示验证码
      var that = this;
      this.$http.get(this.host + this.api.uc.captcha).then(function (res) {
        window.initGeetest(
          {
            // 以下配置参数来自服务端 SDK
            gt: res.body.gt,
            challenge: res.body.challenge,
            offline: !res.body.success, //表示用户后台检测极验服务器是否宕机
            new_captcha: res.body.new_captcha, //用于宕机时表示是新验证码的宕机
            product: "bind",
            width: "100%",
            lang: "en",
          },
          this.handler
        );
      });
    },
    handler(captchaObj) {
      captchaObj
        .onReady(() => {
          $("#wait").hide();
        })
        .onSuccess(() => {
          this.isRegister = true;
          this.success();
        });
      $("#sendCode").click(() => {
        const tel = this.formInline.user,
          // flagtel = mobilereg.test(tel) || emailReg.test(tel);
          flagtel = true;
        flagtel && captchaObj.verify();
        !flagtel && this.$Message.error("请填写正确的手机号或者邮箱号");
      });
    },
    onAreaChange(value) {
      for (var i = 0; i < this.areas.length; i++) {
        if (this.areas[i].zhName == value) {
          this.formInline.areaCode = this.areas[i].areaCode;
        }
      }
    },
    getAreas() {
      this.$http.post(this.host + this.api.common.area).then((response) => {
        var resp = response.body;
        this.areas = resp.data;
        this.formInline.country = this.areas[0].zhName;
        this.formInline.areaCode = this.areas[0].areaCode;
      });
    },
    actives: function (index) {
      this.changeActive = index;
      if (this.changeActive == 0) {
        this.showCode = true;
        this.key = this.$t("uc.regist.telno");
        this.ruleInline.code = [
          {
            required: true,
            message: this.$t("uc.regist.smscodetip"),
            trigger: "blur",
          },
        ];
      } else {
        this.showCode = false;
        this.key = this.$t("uc.regist.email");
        this.ruleInline.code = [];
      }
    },
    // sendCode() {
    //   if(this.changeActive == 0) {
    //     var mobilePhone = this.formInline.user;
    //     // let reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
    //     if (mobilePhone == "") {
    //       this.$Message.error(this.$t("uc.regist.teltip"));
    //       return;
    //     } else {
    //       this.initGtCaptcha();
    //     }
    //   }else{
    //     this.initGtCaptcha();
    //   }
    // },
  },
};
</script>
<style lang="scss">
.login_form {
  .login_right {
    form.ivu-form.ivu-form-label-right.ivu-form-inline {
      text-align: center;
      .ivu-form-item {
        .ivu-form-item-content {
          .ivu-input-wrapper.ivu-input-type {
            .ivu-input {
              border: none;
              border-bottom: 1px solid #27313e;
              font-size: 14px;
              background: transparent;
              border-radius: 0;
              // color:#fff;
              &:focus {
                border: none;
                border-bottom: 1px solid #27313e;
                -moz-box-shadow: 2px 2px 5px transparent,
                  -2px -2px 4px transparent;
                -webkit-box-shadow: 2px 2px 5px transparent,
                  -2px -2px 4px transparent;
                box-shadow: 2px 2px 5px transparent, -2px -2px 4px transparent;
              }
            }
          }
        }
      }
      .check-agree {
        .ivu-checkbox-wrapper {
          .ivu-checkbox-input {
            &:focus {
              border: none;
              outline: none;
              -moz-box-shadow: 2px 2px 5px transparent,
                -2px -2px 4px transparent;
              -webkit-box-shadow: 2px 2px 5px transparent,
                -2px -2px 4px transparent;
              box-shadow: 2px 2px 5px transparent, -2px -2px 4px transparent;
            }
          }
        }
        .ivu-checkbox-wrapper.ivu-checkbox-wrapper-checked {
          .ivu-checkbox.ivu-checkbox-checked {
            .ivu-checkbox-inner {
              border: 1px solid #f0ac19;
              background-color: #f0ac19;
            }
          }
        }
        .ivu-checkbox-wrapper.ivu-checkbox-default {
          .ivu-checkbox {
            .ivu-checkbox-inner {
              background: transparent;
            }
          }
        }
      }
    }
  }
}
</style>
<style>
